import Marquee from "react-fast-marquee";
import "../../styles/Slider.css";

const words = [
  "Le mari a tent un moonwalk chec total ! ",
  "",
  "Le tmoin a oubli son discours Gne palpable. ",
  "",
  "Le bouquet a t lanc 3 blesss lgers. ",
  "",
  ":  (1=A, 5=E, 12=L, etc...)",
  "",
  "Un enfant a dcouvert le bar  bonbons Chaos imminent ! ",
  "",
  "Le traiteur en rupture de champagne Rvolution annonce ! ",
  "",
  "Le photobooth dborde Selfies en surcharge ! ",
  "",
  "La marie en talons dans lherbe Suspense insoutenable ! ",
  "",
  "Une grand-mre a pris le contrle de la piste Respect ternel ! ",
  "",
];

function Slider() {
  return (
      <Marquee duration={30} autoFill="true" gradient="true" gradientColor="#294069" pauseOnHover="true" direction="left" className="slider">
      {words.map((el) => (
          <div className="slide" key={el}>{el}</div>
        ))}
    </Marquee>
  );
}

export default Slider;
